
.title {
    margin-bottom: 20px;
    position: relative;
}
.title::before {
    content: "";
    position: absolute;
    background: #e6bb4e;
    width: 70px;
    height: 1px;
    bottom: -20px;
    left: 50%;
    transform: translate(-50%, 50%);
}
.title h2 {
    margin: 0;
    font-family: 'Josefin Sans', sans-serif;
    font-weight: 400;
    color: black;
    text-transform: capitalize;
    font-size: 40px;
    line-height: 1.2;
    letter-spacing: 5px;
}
